class TvChannelBox extends React.Component {
    classByCategory() {
        let className = "";
        if (this.props.channel && this.props.channel.categoryList) {
            Object.keys(this.props.channel.categoryList).forEach(key => className += 'is-' + key.toLowerCase() + ' ');
        }
        className += "is-" + this.props.channel.code + ' ';

        return className;
    }

    classByPackage() {
        let className = "";
        if (this.props.channel && this.props.channel.packagesList) {
            Object.keys(this.props.channel.packagesList).forEach(key => className += 'is-' + key.toLowerCase() + ' ');
        }
        return className;
    }

    classByResolution() {
        let className = "";
        if (this.props.channel && this.props.channel.resolution) {
            className += 'is-' + this.props.channel.resolution.toLowerCase();
            className += ' is-hdis-4k';
        }
        return className;
    }

    render() {
        let props = this.props;
        // {props.channel.code.includes("HD") && (<span className="opl-package-tv-choose__hd">HD</span>)}
        return (
            <li className={"l-group__element opl-tv-channel-list__element u-margin-right " + this.classByCategory() + ' ' + this.classByPackage() + ' ' + this.classByResolution()}
                data-type="mixer-target" data-phrase={props.channel.name}>
                <div className="opl-checkbox opl-checker__wrapper o-checker__wrapper g-white1-bg"
                     title={props.channel.code}>
                <span className="o-checker u-position-relative u-overflow">
                <span className="opl-channel-star"></span><img
                    src={!("empty" === props.channel.image) ? props.channel.image : "//:0"}
                    alt={props.channel.name}/></span></div>

            </li>)

    }
};



